const canvas = document.getElementById('home');
const ctx = canvas.getContext('2d');

const box1Button = document.getElementById('box1Button');
const box2Button = document.getElementById('box2Button');
const box3Button = document.getElementById('box3Button');
const box4Button = document.getElementById('box4Button');


function renderBox1() {
  // Clear the canvas
  ctx.clearRect(0, 0, canvas.width, canvas.height);

  // Render content for Box 1
  ctx.fillStyle = 'blue';
  ctx.fillRect(50, 50, 200, 200);
  // Add additional content for Box 1
}

function renderBox2() {
  // Clear the canvas
  ctx.clearRect(0, 0, canvas.width, canvas.height);

  // Render content for Box 2
  ctx.fillStyle = 'green';
  ctx.fillRect(300, 50, 200, 200);
  // Add additional content for Box 2
}

function renderBox3() {
  // Clear the canvas
  ctx.clearRect(0, 0, canvas.width, canvas.height);

  // Render content for Box 3
  ctx.fillStyle = 'red';
  ctx.fillRect(50, 300, 200, 200);
  // Add additional content for Box 3
}

function renderBox4() {
  // Clear the canvas
  ctx.clearRect(0, 0, canvas.width, canvas.height);

  // Render content for Box 4
  ctx.fillStyle = 'purple';
  ctx.fillRect(300, 300, 200, 200);
  // Add additional content for Box 4
}



box1Button.addEventListener('click', () => {
    renderBox1();
});

box2Button.addEventListener('click', () => {
    renderBox2();
});

box3Button.addEventListener('click', () => {
    renderBox3();
});

box4Button.addEventListener('click', () => {
    renderBox4();
});

// Set the initial content
renderBox1();
